<script lang="ts">
	import {Delete, Edit} from "./svgs";
	import {editState, collections} from "./../../../stores/collectionStore";
	import {notesAPI} from "../../../api/notesAPI";
	import {push} from "svelte-spa-router";

	export let id = -1;
	export let cover_text = "cover";
	export let title = "title";
	export let isAdd = false;

	const nav = function navOrToggle() {
		if (isAdd || id === -1) {
			editState.toggleIsAdd();
		} else {
			push(`/collection/${id}`);
		}
	};

	const editCol = function editCollection() {
		editState.setId(id);
		editState.toggleIsEdit();
	};

	const delCol = async function deleteCollection() {
		if (id === -1) return;
		let results;
		try {
			// await notesAPI.deleteCollection(id);
			collections.deleteCollection(id);
		} catch (error) {
			alert(error);
		}
		

		// collections.getCollections();
	};
</script>

<div class="container">
	<div class="parent" on:click={nav}>
		{#if !isAdd}
			<div class="delete">
				<button on:click|stopPropagation={delCol}>
					<Delete />
				</button>
			</div>
			<div class="edit">
				<button on:click|stopPropagation={editCol}>
					<Edit />
				</button>
			</div>
		{/if}
		<div class="cover">
			<h1>{cover_text}</h1>
		</div>
	</div>
	<div class="title">
		<span>{title}</span>
	</div>
</div>

