<script lang="ts">
	import {
		editState,
		collections,
		getTitle,
	} from "../../../stores/collectionStore";
	import {notesAPI} from "./../../../api/notesAPI";

	type Type = "add" | "edit";

	export let submitType: Type = "add";

	let title: string = submitType === "add" ? "" : $getTitle;

	const handleSubmit = function handleSubmit() {
		if (submitType === "add") {
			notesAPI
				.addCollection(title)
				.then(async (res) => {
					await collections.getCollections();
					editState.toggleIsAdd();
				})
				.catch((err) => alert(err));
		} else {
			notesAPI
				.editCollection(title, $editState.collectionId)
				.then(async (res) => {
					await collections.getCollections();
					editState.toggleIsEdit();
				})
				.catch((err) => alert(err));
		}
	};
</script>

<section class="addCollection">
	<main class="editForm">
		<div class="formItem">
			<label for="collection-title">TITLE</label>
		</div>
		<div class="formItem">
			<input type="text" id="collection-title" bind:value={title} />
		</div>
		<div class="formItem">
			<slot name="btn1" />

			<button on:click={handleSubmit}>
				<slot />
			</button>
		</div>
	</main>
</section>

