<script lang="ts">
	import {noteEditState} from "../../../stores/collectionStore";
	export let id: number;
	export let content: string;
	export let title: string;
	export let date: Date;
	let noteContentHeight: number;

	const handleClick = () => {
		noteEditState.setId(id);
		noteEditState.toggleIsEdit(noteContentHeight);
	};
</script>

<div class="parent" on:click={handleClick}>
	<div class="title">
		<span>{title}</span>
	</div>
	<div class="bottomBorder"></div>
	<div class="body" bind:offsetHeight={noteContentHeight}>
		<span >{content}</span>
	</div>
	<div class="bottom">
		<span
			>{new Date(date).toLocaleDateString("en-GB", {
				weekday: "short",
				year: "2-digit",
				month: "short",
				day: "2-digit",
				hour: "2-digit",
				minute: "2-digit",
			})}</span
		>
	</div>
</div>

<style lang="scss">
	@use "styles/note_main";
</style>
