<script lang="ts">
	import {
		Collection,
		collections,
		notes,
		noteEditState,
	} from "../../stores/collectionStore";
	import {AddBtn, Note, NoteOverlay, EditComp} from "./components";
	import {onMount} from "svelte";

	export let params: {id?: string} = {};
	let title: Collection;

	onMount(async () => {
		try {
			if (!$collections.length) await collections.getCollections();

			title = $collections.filter((el) => {
				return el.id.toString() === params.id;
			})[0];

			if (title) {
				await notes.getNotes(parseInt(params.id));
			}
		} catch (error) {
			alert(error);
		}
	});
</script>

<section class="parent">
	{#if title}
		<AddBtn />
	{/if}
	<h1 class="title">{title ? title.title : "no collection to show"}</h1>
	<main class="main">
		{#each $notes as note}
			<Note {...note} />
		{:else}
			<img
				class="image"
				src="https://c.tenor.com/Z6gmDPeM6dgAAAAC/dance-moves.gif"
				alt="you know the rules....."
			/>
		{/each}
	</main>
</section>
{#if $noteEditState.isEdit}
	<NoteOverlay close={noteEditState.toggleIsEdit}>
		<EditComp
			type="edit"
			collection_id={parseInt(params.id)}
			closeFN={noteEditState.toggleIsEdit}
			deleteFN={true}
		/>
	</NoteOverlay>
{/if}

{#if $noteEditState.isAdd}
	<NoteOverlay close={noteEditState.toggleIsAdd}>
		<EditComp
			type="add"
			collection_id={parseInt(params.id)}
			closeFN={noteEditState.toggleIsAdd}
		/>
	</NoteOverlay>
{/if}

